import { Alert, Grid, View, useBreakpointValue } from "@aws-amplify/ui-react";
import {
  FormularioVerificacion,
  IlustracionVerificacion,
  NavBarHeaderL,
  SideBar,
} from "../../../../../../ui-components";
import { constantesInterceptors } from "../../../../../../shared/constants/constants";
import { paymentConfirmMock } from "../../../../../../shared/constants/paymentConfMock";
import { useEffect, useState } from "react";
import httpService from "../../../../../../shared/services/httpService";
import Guards from "../../../../../../shared/guard/guards";
import { useNavigate } from "react-router";
import store from "../../../../../../store/store";
import { CalculationPayload } from "../../../../../../shared/models/calculations-payload"
import { PaymentPayload } from "../../../../../../shared/models/payment-payload"
import CurrencyFormatter from "../../../../../../shared/pipes/moneda";
import Dates from "../../../../../../shared/pipes/dates";
import { Routes } from "../../../../../../shared/constants/routes";
import MobileTemplate from "../../../../../base/mobile-template";
import DesktopTemplate from "../../../../../base/desktop-template";
import { StoreConst } from "../../../../../../shared/constants/store";
import { AlertsMsg } from "../../../../../../shared/constants/alerts";

let isOnFligth = false;

function Verification() {
  const history = useNavigate();
  const prevURI = Routes.CONFIGURATION;
  const nextURI = Routes.CONFIRMATION;

  const payConfig = store.getState().paymentConfig;

  const [inputValuePay, setInputValuePay] = useState("");
  const [inputValueDeb, setInputValueDeb] = useState("");
  const [inputValue, setInputValue] = useState("");
  const [alertError, setAlertError] = useState(false);

  const leaderStateStr = isOnFligth ? "loader-en" : "loader-dis"
  const [loaderState, setLoaderState] = useState(leaderStateStr);

  let calcCost: string = "";
  Guards();

  let payload: CalculationPayload = setPayLoad();
  let paymentPayload: PaymentPayload = setPaymentPayload();
  

  payload.data.amount.amount = +payConfig.amount;

  payload.data.source_account.account_id = payConfig.sourceProdObj.account_id;
  payload.data.source_account.account_type =
    payConfig.sourceProdObj.account_type;

  payload.data.destination_account.account_id =
    payConfig.targetProdObj.account_id;
  payload.data.destination_account.account_type =
    payConfig.targetProdObj.account_type;

  paymentPayload.data.source_account.account_id = payConfig.sourceProdObj.account_id;
  paymentPayload.data.source_account.account_type = payConfig.sourceProdObj.account_type;
  paymentPayload.data.source_account.account_subtype = payConfig.sourceProdObj.account_subtype;
  
  paymentPayload.data.loan_id = payConfig.targetProdObj.account_id;
  paymentPayload.data.loan_type = payConfig.targetProdObj.account_type;
  paymentPayload.data.amount.amount = +payConfig.amount;

  paymentPayload.data.debit_transaction_description = "|" + payConfig.targetProdObj.account_type + "|" + payConfig.targetProdObj.account_id + "|" + payConfig.targetProdObj.account_subtype + "|";
  paymentPayload.data.effective_date = Dates.PaymentFormatter(Date.now()) + "T09:58:07.01-05:00";

  useEffect(() => {
    httpService
      .execute(
        constantesInterceptors.postAccountCalculation,
        "",
        "post",
        payload
      )
      .then((response: any) => {
        if(!isOnFligth)
          setLoaderState("loader-dis");
        calcCost = response.transaction_cost.amount;
        store.dispatch({
          type: StoreConst.TRANSACTIONCOST,
          payload: response,
        });
      });
  });

  const contentVariat = useBreakpointValue([
    { hasImage: false, padding: "1rem" },
    { hasImage: false, padding: "1rem" },
    { hasImage: false, padding: "1rem" },
    { hasImage: true, padding: "2rem" },
  ]) as any;

  let container = (
    <View columnStart="1" columnEnd="2" rowStart="2" rowEnd="-1">
      <Grid
        columnGap="0rem"
        rowGap="0rem"
        templateColumns="1fr 1fr"
        templateRows="100fr 5fr"
        padding={contentVariat.padding}
      >
        <View
          columnStart="1"
          columnEnd="1"
          rowStart="1"
          rowEnd="-1"
          margin={"3rem auto"}
        >
          { alertError? <Alert variation="warning">{AlertsMsg.ALERT_PAYMENT_ERROR}</Alert>:<></> }
          <FormularioVerificacion
            loaderState={loaderState}
            onClickBack={() => {
              history(Routes.HOME);
            }}
            tarjetProd={payConfig.targetProd}
            sourceProd={payConfig.sourceProd}
            payAmount={CurrencyFormatter(payConfig.amount)}
            trxCost={CurrencyFormatter(calcCost)}
            nextURI={nextURI}
            prevURI={prevURI}
            onClickNext={async () => {
              let paymentConfirmation = {};

              setLoaderState("loader-ena");
              isOnFligth = true;
              await httpService.execute(
                  constantesInterceptors.LoanPaymet,
                  "",
                  "post",
                  paymentPayload
                ).then(async (response: any) => {
                  isOnFligth = false;
                  setLoaderState("loader-dis");

                  if(response && response != "") {
                    paymentConfirmation = response.payment[0];
                    await store.dispatch({
                      type: "payment",
                      payload: paymentConfirmation,
                    });
                    history(Routes.CONFIRMATION);
                  }
                  else {
                    setAlertError(true);
                    setTimeout(()=>{
                      setAlertError(false);
                    }, 5000);
                  }
                }).catch(async (e) => {
                  isOnFligth = false;
                  setLoaderState("loader-dis");
                  setAlertError(true);
                  setTimeout(()=>{
                    setAlertError(false);
                  }, 5000);
              });
            }}
            onClickCancel={() => {
              history(Routes.HOME);
            }}
            onChangePay={(e: any) => {
              setInputValuePay(e.target.value);
            }}
            onChangeDeb={(e: any) => {
              setInputValueDeb(e.target.value);
            }}
            onChangeInput={(e: any) => {
              setInputValue(e.target.value);
            }}
          />
        </View>

        {
          contentVariat.hasImage ? 
            <View
              columnStart="2"
              columnEnd="-1"
              rowStart="1"
              rowEnd="1"
              margin={"auto"}
            >
              <IlustracionVerificacion />
            </View>
            :<></>
        }
        
      </Grid>
    </View>
  );

  const variation = useBreakpointValue([
    <MobileTemplate props={{content: container}}/>,
    <MobileTemplate props={{content: container}}/>,
    <MobileTemplate props={{content: container}}/>,
    <DesktopTemplate props={{content: container}}/>,
  ]);
  
  return (
    <>
      {variation}
    </>
  );
}

